$size-pig: 160px;

.main-title {
  background-image: linear-gradient(104deg, #ffdda6, #ffbe56);
  font-family: SFProDisplay;
  font-size: 24px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.25;
  letter-spacing: normal;
  text-align: center;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-left: 24px;
  margin-right: 24px;
  white-space: pre-wrap;
}

.main-button-wrapper {
  display: flex;
  margin: 24px 32px 18px 32px;
  align-self: stretch;
  .button {
    width: 100%;
    height: 40px;
    border-radius: 20px;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-image: linear-gradient(99deg, #ffda7b, #d69227);
    .title {
      font-family: SFProDisplay;
      font-size: 16px;
      font-weight: 600;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      color: #fcfcfc;
      text-transform: none;
    }
  }
}

.main-button {
  width: 100%;
  height: 40px;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-image: linear-gradient(99deg, #ffda7b, #d69227);
  margin: 24px 32px 18px 32px;
  .title {
    font-family: SFProDisplay;
    font-size: 16px;
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #fcfcfc;
    text-transform: none;
  }
}
.main-button-round {
  width: auto;
  align-self: stretch !important;
  margin: 24px 32px 18px 32px !important;
  border-radius: 40px !important;
}

.introduce {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-image: url('./images/bg-top@3x.png');
  background-size: 100%;
  .header {
    flex-direction: column;
    display: flex;
    align-items: center;

    > .title {
      @extend .main-title;
    }
    .hunter-button {
      @extend .main-button;
      max-width: 256px;
    }
    .guide-button {
      flex-direction: column;
      display: flex;
      align-items: center;
      margin: 0px 0px 18px 0px;
      .title {
        font-family: SFProDisplay;
        font-size: 15px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.33;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
      }
      .down-image {
        width: 12px;
        height: 12px;
        margin-top: 6px;
      }
    }
    .gift-wapper {
      flex-direction: column;
      display: flex;
      align-items: center;
      .title {
        @extend .main-title;
      }
      .description {
        font-family: SFProDisplay;
        font-size: 15px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.33;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
        margin: 12px 24px 12px 24px;
        white-space: pre-wrap;
      }
    }
  }

  .pig-image {
    width: $size-pig;
    height: $size-pig;
    margin-top: 100px;
    margin-bottom: 20px;
  }
  .red-packet {
    background-size: 100%;
    width: 100%;
  }
}

.background-footer {
  background-image: url('./images/bg-footer@3x.png');
  background-size: 100%;
  background-position: bottom;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.guide {
  margin: 16px 16px 16px 16px;
  padding: 16px 16px 16px 16px;
  border-radius: 10px;
  box-shadow: 0 10px 50px 0 rgba(0, 0, 0, 0.3);
  background-color: #ffffff;
  align-items: center;
  display: flex;
  flex-direction: column;
  > .title {
    font-family: SFProDisplay;
    font-size: 20px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.25;
    letter-spacing: normal;
    text-align: center;
    color: #1b1d20;
    margin: 24px 24px 24px 24px;
  }
  .row {
    width: 100%;
    display: flex;
    margin: 12px;
    align-items: center;
  }
  .description {
    flex: 1;
    font-family: SFProDisplay;
    font-size: 15px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #1b1d20;
  }
  .image {
    width: 80px;
    height: 80px;
    margin-right: 10px;
  }
  .main-button {
    @extend .main-button;
    border-radius: 10px;
  }
}
